<template>
  <div class="">
    <div class='rad-group radio-cont'>
      <el-radio-group v-model="radio" @change="changeTab">
        <!--<el-radio-button label="all">全部</el-radio-button>-->
        <el-radio-button label="export">待导出</el-radio-button>
        <el-radio-button label="verify">审核中</el-radio-button>
        <el-radio-button label="passed">已通过</el-radio-button>
        <el-radio-button label="reject">已拒绝</el-radio-button>
      </el-radio-group>
    </div>
    <!--<div v-show="radio == 'all'">-->
    <div>
      <div class="handle-box">
        <el-form :inline="true" class="inlineform1">
          <el-form-item label="">
            <el-button v-show="radio == 'export'" type="primary" icon="plus" class="handle-del mr10" @click="outsExport">批量导出</el-button>
            <el-upload v-show="radio == 'verify'" type="primary"
              class="unload-mine"
              :action="uploadUrl"
              name="excel"
              :data="excelsdata"
              :show-file-list="false"
              :on-success="handleSuccess"
              :file-list="fileList">
              <el-button size="small" type="primary">批量导入</el-button>
            </el-upload>
            <el-button v-show="radio == 'verify'" type="primary" icon="plus" class="handle-del mr10" @click="sureReject">确认拒绝</el-button>
            <el-button v-show="radio == 'reject' && showTable " type="primary" :disabled="rejectDisabled" icon="plus" class="handle-del mr10" @click="outsReject">批量导出</el-button>
            <!--<el-button v-show="radio == 'reject'" type="primary" icon="plus" class="handle-del mr10" @click="insReject">批量导入</el-button>-->
            <el-upload v-show="radio == 'reject' && showTable " type="primary"
                       class="unload-mine"
                       :action="uploadUrl"
                       :data="excelsdata"
                       name="excel"
                       :show-file-list="false"
                       :on-success="handleSuccessReject"
                       :file-list="fileList2">
              <el-button size="small" type="primary" :disabled="rejectDisabled">批量导入</el-button>
            </el-upload>
          </el-form-item>
        </el-form>
        <el-form :inline="true" class="inlineform2" v-show="showTable">
          <el-form-item label="">
            <el-input v-model="searchWord" placeholder="请输入申请人或身份证号查找" class="handle-input mr10"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" icon="search" @click="search">查询</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div v-show="showTable">
        <el-table :data="allListData" border style="width: 100%" ref="multipleTable" v-loading="loading" @selection-change="handleSelectionChange" :key="radio">
          <el-table-column type="index" label="编号" width="50"></el-table-column>
          <el-table-column v-if="radio == 'verify'" type="selection" width="55"></el-table-column>
          <el-table-column prop="cert_name" label="申请证书" width="150"></el-table-column>
          <el-table-column prop="name" label="申请人"></el-table-column>
          <el-table-column prop="personal_id" label="身份证号" width="180"></el-table-column>
          <el-table-column v-if="radio == 'passed'" prop="serial_number" label="证书编号" width="150"></el-table-column>
          <el-table-column prop="apply_date" label="申请时间" width="180">
            <template slot-scope="scope">
              {{dateForm(scope.row.apply_date)}}
            </template>
          </el-table-column>
          <el-table-column prop="pay_amount" label="支付金额">
            <template slot-scope="scope">
              {{scope.row.pay_amount + ' 元'}}
            </template>
          </el-table-column>
          <el-table-column prop="apply_status_msg" label="申请状态"></el-table-column>
          <el-table-column v-if="radio == 'passed'" prop="" label="操作">
            <template slot-scope="scope">
              <el-button @click="clickDetailImg(scope.row.cert_id,scope.row.cert_name,scope.row.wechat_id)" type="text" size="small">查看</el-button>
            </template>
          </el-table-column>
          <el-table-column v-if="radio == 'reject'" prop="" label="查看">
            <template slot-scope="scope">
              <el-button @click="clickDetail(scope.row.cert_name,scope.row.name,scope.row.personal_id,scope.row.study_date,scope.row.pay_amount,scope.row.apply_status_msg)" type="text" size="small">详情</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="pagination">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change ="handleCurrentChange"
          :current-page.sync="currentPage"
          layout="sizes,prev, pager, next"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="curPageSize"
          :total="pageTotal">
        </el-pagination>
      </div>
      </div>
      <div class="detail-cont" v-show="!showTable">
        <p>退款详情</p>
        <ul class="detail-msg">
          <li>
            <span>申请证书：</span>
            <p>{{msg.certname}}</p>
          </li>
          <li>
            <span>申请人：</span>
            <p>{{msg.name}}</p>
          </li>
          <li>
            <span>身份证号：</span>
            <p>{{msg.cardid}}</p>
          </li>
          <li>
            <span>申请时间：</span>
            <p>{{msg.date}}</p>
          </li>
          <li>
            <span>支付金额：</span>
            <p>{{msg.price || 0}}</p>
          </li>
          <li>
            <span>退款进度：</span>
            <p>{{msg.status}}</p>
          </li>
        </ul>
      </div>
    </div>

    <div v-show="radio == 'waitexport'">
      <w-export></w-export>
    </div>
    <el-dialog :title="passedCertName" :visible.sync="dialogImgVisible" width="30%" :modal-append-to-body='false'>
      <span class="dialog-img-cont">
        <img :src="passedImg" alt="">
      </span>
      <span slot="footer" class="dialog-footer">
        <!--<el-button @click="dialogImgVisible = false">取 消</el-button>-->
      </span>
    </el-dialog>

  </div>
</template>

<script>
  import wExport from './WaitExport.vue'
  import store from '../store'
export default {
  components:{
    wExport
  },
  data () {
    return {
      radio:'export',
      loading:false,
      searchWord:'',
      dialogImgVisible:false,
      passedImg:'https://tax.caishuidai.com/api/export/image/cert1/20180901000004.png',
      passedCertName:'',
      currentPage:1,
      curPageSize:10,
      pageTotal:0,
      listData:[],
      allListData:[],
      curCertId:this.$route.query.cert_id,

      uploadUrl:this.baseUrl+'/api/v1/admin/excels',
      fileList:[],
      fileList2:[],
      personalIdList:[],
      msg:{
        certname:'无',
        name:'无',
        cardid:'无',
        date:'无',
        price:'无',
        status:'无'
      },
      showTable:true,

      excelsdata:{
        'Authorization':'Bearer '+ localStorage.token
      },
      rejectDisabled:false

    }
  },
  computed:{
  },
  watch:{
    $route(){
      this.curCertId = this.$route.query.cert_id;
      //console.log(this.curCertId);
      this.radio = 'export';
      this.getData('export',this.currentPage,this.curPageSize)
    },
    //curCertId(){
      // this.getData('export',this.currentPage,this.curPageSize)
    //},

  },
  created(){
    //console.log(this.$route.query.cert_id);
    this.curCertId = this.$route.query.cert_id;
    this.getData('export',this.currentPage,this.curPageSize)
  },
  methods:{
    getParams: function () {
      let self = this;
    },
    getData(type='export',page=1,limit=10){
      let self = this;
      self.$axios.get(self.baseUrl + '/api/v1/admin/applicants/certs/' + self.curCertId + '?page=' + page +'&limit='+limit+'&type=' + type).then(res =>{
        if(res.data.success){
          self.allListData = res.data.data.list;
          self.pageTotal = res.data.data.count;
          if(type == 'reject'){
            if(res.data.data.list.length>0 && res.data.data.list[0].pay_amount == '0'){
              self.rejectDisabled = true
            }else{
              self.rejectDisabled = false
            }
          }

        }else{
          self.$message.error(res.data.msg);
        }


      })
    },
    clickDetail(certname,name,cardid,date,price,status){
      let self = this;
      self.showTable = false;
      self.msg.certname = certname;
      self.msg.name = name;
      self.msg.cardid = cardid;
      self.msg.date = date;
      self.msg.price = price + ' 元';
      self.msg.status = status;
    },
    clickDetailImg(certid,certname,wechatid){
      let self = this;
      self.passedCertName = certname;
      self.$axios.get(self.baseUrl + '/api/v1/admin/images/certs/' + certid + '/' + wechatid).then(res =>{
        if(res.data.success){
          self.passedImg = res.data.data.image_url;
          self.dialogImgVisible = true;
        }else{
          self.$message.error(res.data.msg);
        }


      })
    },
    handleSelectionChange(val){
      let arr = [];
      for(let i in val){
        arr.push(val[i].personal_id);
      };
      this.personalIdList = arr;
    },
    handleSizeChange(val) {
      this.curPageSize = val;
      this.getData(this.radio,this.currentPage,this.curPageSize);
    },
    handleCurrentChange(val){
      this.currentPage = val;
      this.getData(this.radio,this.currentPage,this.curPageSize);
    },
    changeTab(){
      this.showTable = true;
      this.getData(this.radio);
    },
    outsExport(){
      let self = this;
      self.$confirm('是否批量导出全部“待导出”数据?', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        self.$axios.get(self.baseUrl+'/api/v1/admin/files/applicants/certs/'+self.curCertId,{
          params:{
            type:'export'
          }
        }).then(res =>{
          if(res.data.success){
            window.location.href = res.data.data.file_url;
            self.$message({message:'导出成功',type:'success'});
            self.getData(self.radio,self.currentPage,self.curPageSize);
          }else{
            self.$message.error(res.data.msg);
          }


        })

      }).catch(() => {

      });



    },
    sureReject(){
      let self = this;
      if(self.personalIdList.length<1){
        self.$message({message:'请选择数据',type:'warning'});
        return false;
      }
      self.$axios.put(self.baseUrl+'/api/v1/admin/applicants/certs/'+self.curCertId,{
        action:'reject',
        pids:self.personalIdList

      }).then(res =>{
        if(res.data.success){
          self.$message({message:'操作成功',type:'success'});
          self.getData(self.radio,self.currentPage,self.curPageSize);
        }else{
          self.$message.error(res.data.msg);
        }


      })

    },
    outsReject(){
      let self = this;
      self.$confirm('是否批量导出全部“已拒绝”数据?', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        self.$axios.get(self.baseUrl+'/api/v1/admin/files/applicants/certs/'+self.curCertId,{
          params:{
            type:'reject'
          }
        }).then(res =>{
          if(res.data.success){
            window.location.href = res.data.data.file_url;
            self.$message({message:'导出成功',type:'success'})
          }else{
            self.$message.error(res.data.msg);
          }


        })

      }).catch(() => {

      });

    },
    insReject(){

    },
    search(){
      let self = this;
      if(self.searchWord == ''){
        self.$message({message:'请输入查询内容',type:'warning'});
        return false;
      }
      self.$axios.get(self.baseUrl + '/api/v1/admin/applicants/certs/' + self.curCertId + '?page=' + self.currentPage +'&limit='+self.curPageSize+'&type=' + self.radio + '&field=' +self.searchWord).then(res =>{
        if(res.data.success){
          self.allListData = res.data.data.list;
          self.pageTotal = res.data.data.count;
        }else{
          self.$message.error(res.data.msg);
        }


      })
    },
    handleSuccessReject(res,file,fileList){
      let self = this;
      if(res.success){
        let excelPath = res.data.excel_save_path;
        self.$confirm('是否确认退款？')
          .then(res => {
            self.getExcelResult('refunded',excelPath);
          })
          .catch(res => {
            self.$message({message:'取消退款',type:'warning'});
          })
      }else{
        self.$message.error(res.data.msg)
      }
    },
    handleSuccess(res,file,fileList){
      let self = this;
      if(res.success){
        let excelPath = res.data.excel_save_path;
        self.getExcelResult('passed',excelPath);
      }else{
        self.$message.error(res.data.msg)
      }
    },
    getExcelResult(status,path){
      let self = this;
      let params = {
        action:status,
        file_path:path
      }
      self.$axios.put(self.baseUrl + '/api/v1/admin/applicants/certs/'+self.curCertId,params).then(function(res){
        if(res.data.success){
          self.$message({message:'操作成功',type:'success'});
          self.getData(self.radio,self.currentPage,self.curPageSize);
        }else{
          self.$message.error(res.data.msg);
        }

      },function(err){
        self.$message.error(err.msg);
      })

    },
    //秒数转为时间戳
    dateForm:function(time){
      let date = new Date(time*1000);
      //return date.toLocaleString();
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? ('0' + m) : m;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      let h = date.getHours();
      h = h < 10 ? ('0' + h) : h;
      let minute = date.getMinutes();
      let second = date.getSeconds();
      minute = minute < 10 ? ('0' + minute) : minute;
      second = second < 10 ? ('0' + second) : second;
      return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;
    },


  }

}
</script>
<style scoped>
  .dialog-img-cont{
    width:100%;
    height:100%;
  }
  .dialog-img-cont img{
    width:100%;
    height:100%;
  }
  .detail-cont{
    margin:20px 0;
  }
  .detail-cont p{
    font-size:22px;
    padding:20px 0;
  }
  .detail-msg li span,.detail-msg li p{
    display: inline-block;
    width:120px;
    text-align: right;
    font-size:16px;
    color:#666;
  }
  .detail-msg li p{
    text-align: left;
  }
  .handle-box{  margin-top: 20px; }
  .handle-input{  width: 300px;  display: inline-block; }
  .inlineform2{float: right;}
  .inlineform1{float: left;}
  .pagination{margin:20px 0;text-align: right;}
  .radio-cont{
    margin-bottom:20px;
  }
  .unload-mine{
    width:98px;
    height:40px;
    font-size:14px;
    margin:0 20px;
    display: inline-block;
  }
  .unload-mine>>>.el-upload{
    width:100%;
    height:100%;
  }
  .unload-mine>>>.el-button--small{
    width:100%;
    height:100%;
    font-size:14px;
  }
</style>



